import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  value: "INR",
  symbol: "",
};

export const cryptoSlice = createSlice({
  name: "crypto",
  initialState,
  reducers: {
    changeCurrency: (state) => {
      if (state.value === "INR") {
        state.value = "USD";
        state.symbol = "$";
      } else if (state.value === "USD") {
        state.value = "INR";
        state.symbol = "";
      }
    },
  },
});

export const { changeCurrency } = cryptoSlice.actions;

export default cryptoSlice.reducer;
