import {
  AppBar,
  Container,
  MenuItem,
  Select,
  Toolbar,
  Typography,
} from "@mui/material";
import { makeStyles } from "@mui/styles";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { changeCurrency } from "../features/crypto/cryptoSlice";

const useStyles = makeStyles({
  title: {
    flex: 1,
    color: "#00ffa1",
    cursor: "pointer",
  },
});

const Header = () => {
  const classes = useStyles();
  let navigate = useNavigate();

  const currency = useSelector((state) => state.crypto.value);
  const dispatch = useDispatch();

  return (
    <AppBar color="transparent" position="static">
      <Container>
        <Toolbar>
          <Typography
            variant="h6"
            className={classes.title}
            onClick={() => navigate("/")}
            style={{
              fontFamily: "Montserrat",
              fontWeight: "bold",
            }}
          >
            Crypto Monster
          </Typography>
          <Select
            value={currency}
            onChange={() => {
              dispatch(changeCurrency());
            }}
            style={{
              width: 100,
              height: 40,
              marginRight: 15,
              color: "#fff",
              border: "1px solid #00ffa1",
            }}
          >
            <MenuItem value={"INR"}>INR</MenuItem>
            <MenuItem value={"USD"}>USD</MenuItem>
          </Select>
        </Toolbar>
      </Container>
    </AppBar>
  );
};

export default Header;
